@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
html {
  height: 100%;
  background: #e7e5e5;
}

body {
  width: 320px;
  min-height: 100%;
  margin: auto;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
  background: #ffffff;
}
@media (min-width: 768px) {
  body {
    width: 768px;
  }
}
@media (min-width: 1280px) {
  body {
    width: 1280px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header {
  min-height: 56px;
  position: relative;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
  gap: 25px;
}

.main-header__logo,
.main-header__logo-image {
  width: 185px;
  position: absolute;
  margin: 0 auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.main-nav {
  position: relative;
  display: flex;
  align-items: center;
}

.main-nav__button {
  position: absolute;
  left: 120px;
  right: auto;
  margin: auto 0;
  border: none;
  background-color: transparent;
}
@media (min-width: 768px), (min-width: 1280px) {
  .main-nav__button {
    display: none;
  }
}

.main-nav__wrapper {
  position: relative;
  width: 320px;
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    width: 768px;
  }
}

.main-nav__list {
  position: absolute;
  top: -43px;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 223px;
  list-style-type: none;
  padding: 0;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
}
@media (min-width: 768px), (min-width: 1280px) {
  .main-nav__list {
    position: static;
    flex-direction: row;
    min-height: 56px;
    margin: 0;
    filter: none;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    align-items: center;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-areas: "a b . c d";
  }
}

@media (min-width: 768px), (min-width: 1280px) {
  .main-nav__list-item--1 {
    grid-area: a;
  }
}
@media (min-width: 768px), (min-width: 1280px) {
  .main-nav__list-item--2 {
    grid-area: b;
  }
}
@media (min-width: 768px), (min-width: 1280px) {
  .main-nav__list-item--3 {
    grid-area: c;
  }
}
@media (min-width: 768px), (min-width: 1280px) {
  .main-nav__list-item--4 {
    grid-area: d;
  }
}

.main-nav__list-item-link {
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;
  text-decoration: none;
  width: 100%;
  padding: 17px 80px 12px;
  color: #ffffff;
  border-top: 1px solid rgba(0, 0, 0, 0.25);
  background: #81b3d2;
  box-sizing: border-box;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
}
@media (min-width: 768px), (min-width: 1280px) {
  .main-nav__list-item-link {
    padding: 17px 5px;
    color: #000000;
    border-top: none;
    background: none;
  }
}

