}

.page-main__groop-mark-visited {
  position: absolute;
  width: 46px;
  height: 46px;
  background: #f2f2f2;
  border-radius: 4px;
}

.page-main__groop-input-visited:checked + .page-main__groop-mark-visited {
  background-image: url("./img/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.page-main__groop-input-visited:focus + .page-main__groop-mark-visited {
